<template>
  <video
    :id="id"
    class="video"
    :width="width"
    :height="height"
    x5-video-player-type="h5-page"
    x5-video-player-fullscreen="true"
    x5-video-player-orientation="portraint"
    playsinline="true"
    webkit-playsinline="true"
    controlslist="nodownload noremoteplayback"
    disablePictureInPicture
    :poster="posterUrl"
    controls
    :autoplay="autoplay"
  >
    <source
      :src="videoUrl"
      type="video/mp4"
    >
  </video>
</template>
<script>
/**
 * <Video
      width="100%"
      height="146"
      video-url="https://baidu.com/czone/child-health/ad001.mp4"
    />
 */
  export default {
    name: 'RzVideo',
    components: {},
    props: {
      // id
      id: {
        type: String,
        default: '',
      },
      // 
      title: {
        type: String,
        default: '',
      },
      // 
      length: {
        type: Number,
        default: 0,
      },
      width: {
        type: String,
        default: '100%',
      },
      height: {
        type: String,
        default: '100%',
      },
      // 
      videoUrl: {
        type: String,
        default: null,
      },
      // 
      posterUrl: {
        type: String,
        default: '',
      },
      // 
      autoplay: {
        type: String,
        default: null,
      },
    },
    data() {
      return {};
    },
    watch: {},
    created() {},
    mounted() {
      // id
      const myVideo = document.getElementById(this.id);
      // 
      myVideo.addEventListener('play', () => {
        // idvue
        this.$emit('onplay', this.id);
      });
      // /audio/video
      myVideo.addEventListener('timeupdate', () => {
      // 
      // const timeDisplay = Math.floor(myVideo.currentTime);
      // console.log(Math.floor(myVideo.currentTime));
      });
      // 
      myVideo.addEventListener('pause', () => {});
    },
    methods: {},
  };
</script>
<style scoped lang='scss'>
.video {
  border-radius: 6px;
}
</style>
